body{
  font-family: Arial, serif;
  font-size: 16px;
  line-height: 18px;
  .header {
    &__nav{
      .container {
        .row {
          .col-lg-1 {
            ul{
              li{
                a {
                  padding: 0 5px;
                  display: flex;
                  align-items: center;
                  position: relative;
                  font: .875em/50px "Arial";
                  color: #777a79;
                  text-decoration: none;
                  z-index: 10;
                }
                ul {
                  display: none;
                  flex-direction: column;
                  position: absolute;
                  top: 40px;
                  background: #fff;
                  z-index: 10;
                  li{
                    a{
                      line-height: 30px;
                    }
                  }
                }
              }
            }
            .language{
              li{
                position: relative;
                &:hover{
                  ul{
                    display: flex;
                  }
                }
              }
            }
          }
          .col-lg-5 {
            ul {
              display: flex;
              justify-content: space-between;
              li {
                a {
                  display: flex;
                  align-items: center;
                  font: .875em/50px "Arial";
                  color: #777a79;
                  text-decoration: none;
                  &:hover{
                    text-decoration: underline;
                  }
                }
                ul{
                  width: 100px;
                  display: none;
                  flex-direction: column;
                  position: absolute;
                  top: 40px;
                  background: #fff;
                  z-index: 10;
                  li{
                    text-align: center;
                    a{
                      display: inline-block;
                      line-height: 30px;
                    }
                  }
                }
              }
            }
            .nav{
              li{
                position: relative;
                &:hover{
                  ul{
                    display: flex;
                  }
                }
              }
            }
            .mobile__nav{
              display: none;
              position: relative;
              left: -15px;
              text-align: center;
              #mobileNav,
              #mobileNavClose{
                font-size: 1.5em;
                line-height: 50px;
                color: #777a79;
                text-decoration: none;
              }
              ul{
                width: 200px;
                flex-direction: column;
                position: absolute;
                top: 50px;
                right: -35px;
                background-color: #fff;
                box-shadow: 5px 5px 15px #c4c4c4;
                z-index: 210;
                li{
                  a{
                    padding: 0 0 0 25px;
                    border-top: 1px solid #c4c4c4;
                    border-bottom: 1px solid #c4c4c4;
                  }
                }
              }
            }
          }
        }
      }
    }
    &__popups{
      display: none;
    }
    &__headline{
      height: 135px;
      background-color: #e6e6e6;
      .container{
        .row{
          .col-lg-12{
            .headline{
              width: 100%;
              height: 135px;
              display: flex;
              justify-content: space-between;
              align-items: center;
              background: url("../img/SH.png") no-repeat;
              background-size: 100% 100%;
              .logo {
                width: 50%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                a {
                  width: 310px;
                  height: 100%;
                  display: block;
                  background-image: url("../img/logo.png");
                  background-size: 100% 100%;
                }
                div {
                  position: relative;
                  left: -20px;
                  p {
                    &:nth-child(1) {
                      margin: 0 0 0 20px;
                      font: 1.188em/1em "BravoSC";
                      color: #00548f;
                      text-align: left;
                    }
                    &:nth-child(2) {
                      font: 1.5em/1em "BravoSC";
                      color: #00548f;
                      text-align: left;
                      white-space: nowrap;
                    }
                  }
                }
              }
              .search{
                width: 50%;
                height: 100%;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                input{
                  width: 460px;
                  height: 33px;
                  padding: 0 0 0 15px;
                  font: .875em/33px "Arial";
                  color: #9fa1a2;
                  background-color: #f5f5f5;
                  border: 1px solid #c4c4c4;
                }
                i{
                  width: 37px;
                  height: 33px;
                  line-height: 33px;
                  text-align: center;
                  background: #fff;
                  border-top: 1px solid #c4c4c4;
                  border-right: 1px solid #c4c4c4;
                  border-bottom: 1px solid #c4c4c4;
                }
              }
            }
          }
        }
      }
    }
    &__mainNav{
      background-color: #009fff;
      .container{
        .row{
          .col-lg-12{
            ul{
              display: flex;
              justify-content: space-between;
              li{
                width: 100%;
                position: relative;
                a{
                  height: 60px;
                  padding: 0 5px;
                  display: flex;
                  flex-direction: column;
                  justify-content: center;
                  align-items: center;
                  white-space: nowrap;
                  position: relative;
                  text-decoration: none;
                  span{
                    display: block;
                    font: .813em/1.063em "Arial";
                    text-align: center;
                    text-transform: uppercase;
                    white-space: nowrap;
                    &:nth-child(1){
                      font-weight: bold;
                      color: #fff;
                    }
                    &:nth-child(2){
                      color: #9fdbff;
                    }
                  }
                  &:hover{
                    background-color: #007eec;
                  }
                }
                img{
                  display: none;
                }
              }
              .active{
                background-color: #007eec;
                img{
                  width: 100%;
                  height: 40px;
                  display: block;
                  position: absolute;
                  bottom: -30px;
                  left: 0;
                }
              }
            }
            .mobile__mainNav{
              display: none;
              justify-content: space-between;
              position: relative;
              z-index: 200;
              #mobileMainNav,
              #mobileMainNavClose{
                padding: 0 5px;
                font-size: 1.5em;
                line-height: 60px;
                color: #fff;
                text-decoration: none;
                &:hover{
                  cursor: pointer;
                }
              }
              ul{
                width: 300px;
                flex-direction: column;
                position: absolute;
                top: 60px;
                left: -15px;
                background-color: #009fff;
                z-index: 20;
              }
              .search{
                width: 100%;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                input{
                  width: 90%;
                  height: 33px;
                  padding: 0 0 0 15px;
                  font: .875em/33px "Arial";
                  color: #9fa1a2;
                  background-color: #f5f5f5;
                  border: 1px solid #c4c4c4;
                }
                i{
                  width: 37px;
                  height: 33px;
                  line-height: 33px;
                  text-align: center;
                  background: #fff;
                  border-top: 1px solid #c4c4c4;
                  border-right: 1px solid #c4c4c4;
                  border-bottom: 1px solid #c4c4c4;
                }
              }
            }
          }
        }
      }
    }
  }

  #signInPopup,
  #logInPopup{
    padding: 15px;
    width: 315px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff;
    h6{
      width: 100%;
      font: 1.25em/2em "Arial";
      font-weight: bold;
      text-align: center;
      text-transform: uppercase;
      background-color: #e6e6e6;
    }
    form{
      ul{
        width: 100%;
        li{
          margin: 10px 0;
          display: flex;
          justify-content: space-between;
          align-items: center;
          p{
            font: .875em/1.25em "Arial";
            color: #525252;
            white-space: nowrap;
          }
          .important{
            font-weight: bold;
          }
          label{
            display: flex;
            span{
              font-size: 1.75em;
              line-height: 25px;
              color: #ff5a5a;
            }
            input{
              padding: 0 0 0 5px;
              width: 180px;
              height: 25px;
              font: .875em/25px "Arial";
              color: #525252;
              border: 1px solid #c7c7c7;
              outline: none;
            }
            .error{
              border-color: #e06565;
            }
          }
        }
      }
      button{
        margin: 20px 0 0;
        width: 100%;
        display: block;
        font: 1.125em/35px "Arial";
        font-weight: bold;
        color: #fff;
        text-align: center;
        text-decoration: none;
        background-color: #009fff;
        border: none;
        outline: none;
        &:hover{
          background-color: #007eec;
        }
      }
    }
  }
  #authorMethod{
    padding: 15px;
    width: 315px;
    background-color: #fff;
    h6{
      width: 100%;
      font: 1.25em/2em "Arial";
      font-weight: bold;
      text-align: center;
      text-transform: uppercase;
      background-color: #e6e6e6;
    }
    p{
      span{
        margin: 20px 0 0;
        width: 100%;
        display: block;
        font: 1.125em/35px "Arial";
        font-weight: bold;
        color: #fff;
        text-align: center;
        text-decoration: none;
        background-color: #009fff;
        border: none;
        outline: none;
        &:hover{
          cursor: pointer;
          background-color: #007eec;
        }
      }
    }
  }
}