            &__cart{
              margin: 15px 0;
              width: 100%;
              display: flex;
              flex-wrap: wrap;
              justify-content: space-between;
              .cart{
                margin: 15px 0;
                width: 24%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                position: relative;
                border: 1px solid #dfdfdf;
                a{
                  display: block;
                  text-decoration: none;
                  position: relative;
                  .freeShiping,
                  .onSale{
                    display: inline-block;
                    position: absolute;
                    font: 12px/14px "Arial";
                    color:#fff;
                    text-align: center;
                    background-color: #ff0000;
                    transform: rotate(-45grad);
                    &:after{
                      content: "";
                      width: 0;
                      height: 0;
                      position: absolute;
                      top: -1px;
                      left: -1px;
                    }
                    &:before{
                      content: "";
                      width: 0;
                      height: 0;
                      position: absolute;
                      top: -1px;
                      right: -1px;
                    }
                  }
                  .freeShiping{
                    width: 80px;
                    top: 1px;
                    left: -19px;
                    &:after{
                      border-top: 29px solid #fff;
                      border-right: 25px solid transparent;
                    }
                    &:before{
                      border-top: 29px solid #fff;
                      border-left: 34px solid transparent;
                    }
                  }
                  .onSale{
                    width: 60px;
                    top: 7px;
                    left: -12px;
                    &:after{
                      border-top: 15px solid #fff;
                      border-right: 13px solid transparent;
                    }
                    &:before{
                      border-top: 15px solid #fff;
                      border-left: 17px solid transparent;
                    }
                  }
                  img{
                    width: 100%;
                    padding: 15px 20px;
                  }
                  p{
                    padding: 15px 20px;
                    min-height: 100px;
                    display: block;
                    font: .875em/1em "Arial";
                    font-weight: bold;
                    color: #0081ef;
                  }
                }
                ul{
                  display: flex;
                  li{
                    a{
                      display: block;
                      font: .875em/40px "Arial";
                      font-weight: bold;
                      color: #777;
                      text-align: center;
                      background-color: #eee;
                      border-top: 1px solid #dfdfdf;
                    }
                  }
                  .inCart{
                    width: 61%;
                  }
                  .heart,
                  .exchange{
                    width: 50px;
                    border-left: 1px solid #dfdfdf;
                  }
                }
                .hover{
                  display: none;
                  width: 101%;
                  position: absolute;
                  bottom: -216px;
                  left: -1px;
                  background-color: #fff;
                  border: 1px solid #78afeb;
                  border-top: none;
                  box-shadow: 5px 5px 15px #cecece;
                  z-index: 5;
                  .descr{
                    padding: 10px 20px;
                    display: flex;
                    flex-direction: column;
                    li{
                      p{
                        font: .875em/1.375em "Arial";
                        font-weight: bold;
                        span{
                          padding: 0 0 0 5px;
                          font-weight: normal;
                        }
                      }
                    }
                  }
                  .about{
                    padding: 0 20px 15px;
                    font: .875em/1em "Arial";
                    color: #636363;
                  }
                }
                &:hover{
                  border-color: #78afeb;
                  border-bottom-color: #dfdfdf;
                  box-shadow: 5px 5px 15px #cecece;
                  .hover{
                    display: block;
                  }
                }
              }
