              ul{
                display: flex;
                justify-content: center;
                li{
                  margin: 0 5px;
                  width: 45px;
                  height: 40px;
                  background-color: #efefef;
                  border: 1px solid #dedede;
                  span{
                    display: block;
                    font: 1.438em/40px "Arial";
                    font-weight: bold;
                    color: #0081ef;
                    text-align: center;
                    i{
                      font-weight: bold;
                    }
                    &:hover{
                      cursor: pointer;
                    }
                  }
                }
                .active{
                  background-color: #fff;
                  border-color: #0081ef;
                }
              }
              .showMore{
                width: 350px;
                display: none;
                font: 1.188em/40px "Arial";
                font-weight: bold;
                color: #0081ef;
                text-align: center;
                text-transform: uppercase;
                text-decoration: none;
                background-color: #efefef;
                border: 1px solid #dedede;
                &:hover{
                  background-color: #fff;
                }
              }
