  .filterPopup{
    width: 295px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    background-color: #fff;
    #filterClose{
      position: absolute;
      top: -15px;
      right: -15px;
      color: #c0c0c0;
      &:hover{
        cursor: pointer;
      }
    }
    h6{
      width: 100%;
      height: 50px;
      font: 1.5em/50px "Arial";
      color: #fff;
      text-align: center;
      background-color: #0081ef;
    }
    div{
      width: 100%;
      p {
        margin: 0 0 0 20px;
        font: 1em/1.5em "Arial";
      }
      ul{
        margin: 10px 0;
        width: 100%;
        display: flex;
        justify-content: center;
        li{
          label{
            input{
              padding: 0 0 0 5px;
              width: 90px;
              background: #dadada;
              border: 1px solid #555c5f;
            }
          }
        }
      }
    }
    .price{
      margin: 10px 0;
      .priceRange{
        margin: 0 10px;
        width: 275px;
        .ui-widget-header{
          background: #0081ef!important;
        }
      }
    }
    .type{
      ul{
        flex-direction: column;
        li{
          label{
            input{
              margin: 5px 0 5px 35px;
              width: unset;
            }
          }
        }
      }
    }
    button{
      margin: 15px 0 15px 210px;
      width: 50px;
      height: 30px;
      font: 14px/30px "Arial";
      font-weight: bold;
      color: #fff;
      text-transform: uppercase;
      background-color: #0081ef;
      border-radius: 5px;
      border: none;
      outline: none;
    }
  }
